.first {
  font-family: Poppins;
  min-height: 100vh;
  max-width: 100vw;
  background: url(../assets/img/gardener.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  flex-shrink: 0; }

.first__header {
  font-size: 60px;
  font-weight: 700;
  line-height: 70px;
  color: #ffffff;
  text-align: right;
  margin: 0;
  margin-right: 100px; }

.first__link {
  margin-top: 51px;
  margin-right: 100px;
  text-align: right; }

.first__link--first {
  box-sizing: border-box;
  width: 263px;
  height: 82px;
  border: 1px solid #707070;
  background-color: #a3c145;
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  line-height: 70px;
  text-align: right;
  padding: 26px 29px; }

.first__link--second {
  width: 263px;
  min-height: 82px;
  border: 1px solid #707070;
  background-color: #ffffff;
  color: #a3c145;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  line-height: 70px;
  padding: 26px 29px;
  margin-left: 35px;
  white-space: nowrap;
  margin-top: 20px; }

.second {
  display: block;
  height: 100vh;
  font-family: Poppins; }

.second__header {
  margin-top: 82px;
  font-size: 24px;
  font-weight: 500;
  text-align: center; }

.second__header--description {
  margin-top: 8px;
  font-size: 10px;
  text-align: center; }

.box {
  width: 300px;
  height: 400px;
  border: 1px solid black; }

.box__image {
  width: 300px;
  height: 200px; }

.second__flex {
  margin-top: 40px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around; }

html {
  box-sizing: border-box;
  font-size: 16px; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0; }

.side-nav {
  position: fixed;
  bottom: 50vh;
  display: flex;
  flex-flow: column nowrap; }
  .side-nav__button-tel {
    display: block;
